.loader {
  --shadow: rgba(79, 46, 232, .28);
  --text: var(--categories-current-color);
  --page: rgba(255, 255, 255, .36);
  --page-fold: rgba(255, 255, 255, .52);
  --duration: 3s;
  width: 200px;
  height: 140px;
  text-align: center;
  background: linear-gradient(135deg, #5f3ff2, #4f2ee8);
  border-radius: 13px;
  position: relative;
}

.loader:before, .loader:after {
  --r: -6deg;
  content: "";
  width: 120px;
  box-shadow: 0 16px 12px var(--shadow);
  transform: rotate(var(--r) );
  position: absolute;
  top: 80%;
  bottom: 8px;
}

.loader:before {
  left: 4px;
}

.loader:after {
  --r: 6deg;
  right: 4px;
}

.loader div {
  width: 100%;
  height: 100%;
  z-index: 1;
  perspective: 600px;
  box-shadow: 0 4px 6px var(--shadow);
  background-image: var(--background);
  border-radius: 13px;
  position: relative;
